
.search-nav {
  position: relative;
  width: 100%;
  height: 2rem;
  background-color: #ccc;
  z-index: 4;
}
.search-nav input {
  width: 14.6rem;
  height: 1.4rem;
  margin: 0.3rem 0.7rem;
  border: none;
  border-radius: 0.4rem;
  outline: none;
  font-size: 0.6rem;
  padding: 0 0.5rem 0 1.4rem;
  box-sizing: border-box;
}
.search-nav i {
  position: absolute;
  left: 0.7rem;
  top: 0.3rem;
  width: 1.4rem;
  height: 1.4rem;
  color: #808080;
  text-align: center;
  line-height: 1.4rem;
  font-size: 0.6rem;
}


