
.check-item {
  width: 100%;
  height: auto;
  background: #fff;
  overflow: hidden;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ccc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 0.3rem;
}
.check-item:active {
  background: #f7f7f7;
}
.check-item .left-info {
  width: 14rem;
  padding: 0.3rem 0.5rem;
  display: inline-block;
  box-sizing: border-box;
}
.check-item .left-info p {
  height: 1rem;
  line-height: 1rem;
  font-size: 0.65rem;
}
.check-item .left-info p:first-child {
  height: 1.2rem;
  line-height: 1.2rem;
}
.check-item .left-info p a {
  color: #4fa4d9;
}
.check-item .fa-chevron-right {
  width: 2rem;
  height: inherit;
  display: inline-block;
  text-align: center;
  font-size: 0.7rem;
}


