
.set-item {
  width: 100%;
  height: auto;
  display: block;
  padding: 0.5rem 0;
  border-top: 1px solid #ccc;
}
.set-item p {
  font-size: 0.7rem;
  width: 100%;
  height: 1.2rem;
  line-height: 1.2rem;
  display: block;
  padding-left: 2.5rem;
  box-sizing: border-box;
}
.set-item p:active:not(.title) {
  background: #f0f0f0;
}
.set-item .title {
  padding-left: 1rem;
}


