
.check {
  width: 16rem;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  font-size: 0.6rem;
}
.check .main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-top: 2.4rem;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
}
.check .check-manage {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2.4rem;
  z-index: 2;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: #fff;
  border-top: 1px solid #ccc;
  box-sizing: border-box;
}
.check .check-manage a {
  width: 50%;
  color: #808080;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.check .check-manage a .fa {
  height: 0.7rem;
}
.check .check-manage .choose {
  color: #4fa4d9;
}
.check .shadow {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #808080;
  z-index: 9;
  opacity: 0.7;
}


