
.my-check {
  width: inherit;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #f7f7f7;
}
.my-check .head {
  font-size: 0.75rem;
}
.my-check .main {
  background-color: #fff;
}
.my-check .main .swiper-container {
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #f2f2f2;
/*background-image url("../../assets/img/alert_bg.png")*/
/*background-repeat no-repeat*/
/*background-size 100% 100%*/
}
.my-check .main .swiper-container .title {
  text-align: center;
  font-size: 0.8rem;
  height: 2rem;
  line-height: 2rem;
  box-sizing: border-box;
}
.my-check .main .swiper-container .swiper-slide {
  height: 10px;
}
.my-check .main .swiper-container .swiper-slide .content {
  padding: 0 0.8rem;
  box-sizing: border-box;
}
.my-check .main .swiper-container .swiper-slide .content .field {
  font-size: 0.7rem;
  line-height: 1.2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.my-check .main .swiper-container .swiper-slide .content .field:last-child {
  margin-bottom: 1rem;
}
.my-check .main .swiper-container .swiper-slide .content .field .key {
  width: 5rem;
}
.my-check .main .swiper-container .swiper-slide .content .field .value {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 0.3rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.my-check .main .swiper-container .swiper-slide .content .second-title {
  font-size: 0.75rem;
}
.my-check .main .swiper-container .swiper-slide .content .second-field {
  padding-left: 0.5rem;
  box-sizing: border-box;
}
.my-check .main .swiper-container .swiper-slide-active {
  height: auto;
  overflow-y: auto;
}
.my-check .main .swiper-container .swiper-pagination {
  position: fixed;
  bottom: 0.5rem;
}
.my-check .main .swiper-container .swiper-pagination .swiper-pagination-bullet {
  width: 16px;
  height: 16px;
}
.my-check .main .swiper-container .pag-bottom {
  bottom: 2.9rem;
  height: 1rem;
  background-color: #f2f2f2;
  z-index: 4;
}
.my-check .my-check-main {
  padding-bottom: 3rem;
}
.my-check .footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3rem;
  background-color: #fff;
  z-index: 4;
  border-top: 1px solid #ccc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 0.8rem;
  box-sizing: border-box;
}


