
.com-item {
  width: 100%;
  height: auto;
  background: #fff;
  overflow: hidden;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ccc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 0.3rem;
}
.com-item .left-info {
  width: 15.5rem;
  padding: 0.3rem 0.5rem;
  display: inline-block;
  box-sizing: border-box;
}
.com-item .left-info p {
  line-height: 1.2rem;
  font-size: 0.65rem;
}
.com-item .left-info .button_button {
  height: 1.2rem;
  float: right;
  background-color: #4fa4d9;
}
.com-item .left-info .button_button:active {
  background: #3797d4;
}


