
.manage-index {
  width: inherit;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #f7f7f7;
}
.manage-index .main {
  background-color: #fff;
  padding-bottom: 2.4rem;
}
.manage-index .main .swiper-container {
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #f7f7f7;
/*background-image url("../../assets/img/alert_bg.png")*/
/*background-repeat no-repeat*/
/*background-size 100% 100%*/
}
.manage-index .main .swiper-container .title {
  text-align: center;
  font-size: 0.8rem;
  height: 2rem;
  line-height: 2rem;
  box-sizing: border-box;
}
.manage-index .main .swiper-container .swiper-slide {
  position: relative;
  height: 10px;
}
.manage-index .main .swiper-container .swiper-slide-active {
  height: auto;
  overflow-y: auto;
}
.manage-index .main .swiper-container .swiper-pagination {
  position: fixed;
  bottom: 0.5rem;
}
.manage-index .main .swiper-container .swiper-pagination .swiper-pagination-bullet {
  width: 16px;
  height: 16px;
}
.manage-index .main .swiper-container .pag-bottom {
  bottom: 2.4rem;
  height: 1rem;
  background-color: #f7f7f7;
  z-index: 4;
}
.manage-index .main .pdf {
  width: 100%;
  height: 16rem;
}
.manage-index .main .pdf img {
  width: 2.5rem;
  display: block;
  margin: 4.5rem auto 0.5rem auto;
}
.manage-index .main .pdf p {
  width: 100%;
  display: block;
  text-align: center;
}
.manage-index .main .pdf .name {
  font-size: 0.65rem;
  height: 1.2rem;
  line-height: 1.2rem;
}
.manage-index .main .pdf .a-div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.manage-index .main .pdf a {
  display: inline-block;
  color: #4fa4d9;
  padding: 0 0.5rem;
}
.manage-index .main #guide-price,
.manage-index .main #report,
.manage-index .main #core-index {
  width: 100%;
  height: 16rem;
}
.manage-index .main .subTitle {
  position: absolute;
  right: 0.5rem;
  font-size: 0.5rem;
  top: 0.4rem;
}
.manage-index .footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3rem;
  background-color: #fff;
  z-index: 4;
  border-top: 1px solid #ccc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 0.8rem;
  box-sizing: border-box;
}
.manage-index .pfdIframe {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 6;
}
.manage-index .closePdf {
  position: fixed;
  top: 0;
  right: 0;
  width: 2rem;
  height: 2rem;
  text-align: center;
  line-height: 2rem;
  z-index: 8;
}
.manage-index .closePdf:active {
  background-color: #f7f7f7;
}


