
.commission {
  width: inherit;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #f7f7f7;
}
.commission .com-nav {
  margin-top: -0.3rem;
}
.commission .commission-main {
  padding-top: 6.1rem;
}
.commission .add-com {
  display: block;
  margin: 0.5rem auto;
  padding: 0 0.6rem;
  background-color: #4fa4d9;
}
.commission .add-com:active {
  background: #3797d4;
}


