
.check-flow {
  width: inherit;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #f7f7f7;
  z-index: 4;
}
.check-flow ul li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-bottom: 1px solid #ccc;
}
.check-flow ul li p {
  width: 4rem;
  padding: 0.5rem;
  text-align: center;
  font-size: 0.65rem;
  box-sizing: border-box;
}
.check-flow ul li p:nth-child(1) {
  width: 5.5rem;
}
.check-flow ul li p:nth-child(3) {
  width: 6rem;
}
.check-flow ul .header {
  background-color: #4fa4d9;
}
.check-flow ul .header p {
  color: #fff;
  font-size: 0.75rem;
}


