  background-color: #4fa4d9;
  color: #fff;
  text-align: center;
  font-size: 0.75rem;
}
.com-popup .com-content {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 2rem 0.8rem 3rem 0.8rem;
  font-size: 0.65rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  box-sizing: border-box;
}
