
.hint-popup {
  position: fixed;
  width: 13.8rem;
  height: 10.5rem;
  background-color: #fff;
  z-index: 10;
  border-radius: 0.5rem;
  left: 50%;
  top: 45%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.hint-popup .hint-title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1.8rem;
  line-height: 1.8rem;
  border-radius: 0.5rem 0.5rem 0 0;
  background-color: #4fa4d9;
  color: #fff;
  text-align: center;
  font-size: 0.75rem;
}
.hint-popup .hint-content {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 2rem 0.8rem 2.5rem 0.8rem;
  font-size: 0.65rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  box-sizing: border-box;
}
.hint-popup .hint-content .item-title {
  width: 100%;
  color: #808080;
  font-size: 0.65rem;
}
.hint-popup .hint-content .right-info {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  padding-left: 1rem;
  box-sizing: border-box;
}
.hint-popup .hint-content .right-info p {
  font-size: 0.7rem;
  height: 1.2rem;
}
.hint-popup .hint-content .right-info p:first-child {
  margin-top: 1rem;
}
.hint-popup .hint-content .right-info p a {
  color: #4fa4d9;
}
.hint-popup .hint-foot {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2.5rem;
  border-radius: 0 0 0.5rem 0.5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 0 2.5rem;
  box-sizing: border-box;
}
.hint-popup .hint-foot .button_button {
  height: 1.4rem;
}


