
.choose-popup {
  position: fixed;
  width: 12rem;
  height: 16.8rem;
  background-color: #fff;
  z-index: 10;
  border-radius: 0.5rem;
  left: 50%;
  top: 45%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.choose-popup .popup-title {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 1.8rem;
  line-height: 1.8rem;
  border-radius: 0.5rem 0.5rem 0 0;
  background-color: #4fa4d9;
  z-index: 2;
  color: #fff;
  text-align: center;
  font-size: 0.75rem;
}
.choose-popup .organization {
  position: absolute;
  top: 0;
  left: 0;
  padding: 2rem 0.8rem 3rem 0.8rem;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  box-sizing: border-box;
}
.choose-popup .popup-foot {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 3rem;
  background-color: #fff;
  z-index: 2;
  border-radius: 0 0 0.5rem 0.5rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 2.5rem;
  box-sizing: border-box;
}
.choose-popup .popup-foot .button_button {
  height: 1.4rem;
}


