
.set-manage {
  width: inherit;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #f7f7f7;
}
.set-manage .organization .com-name {
  position: relative;
  font-size: 0.7rem;
  height: 1.2rem;
  line-height: 1.2rem;
}
.set-manage .organization .com-name:before {
  position: absolute;
  top: 0.6rem;
  left: 0;
  width: 2.8rem;
  height: 2px;
  content: '';
  display: block;
  background: #ccc;
}
.set-manage .organization .com-name:after {
  position: absolute;
  top: 0.6rem;
  right: 0;
  width: 9.1rem;
  height: 2px;
  content: '';
  display: block;
  background: #ccc;
}
.set-manage .organization .com-name span {
  margin-left: 3rem;
  font-size: 0.75rem;
}
.set-manage .organization .org {
  padding: 0.5rem 1.2rem;
}
.set-manage .new-com-foot {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2.4rem;
  background-color: #f7f7f7;
  z-index: 4;
  border-top: 1px solid #ccc;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 0.8rem;
  box-sizing: border-box;
}
.set-manage .new-com-foot .foot-button {
  padding: 0 0.8rem;
}


