
.set-index {
  width: inherit;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #f7f7f7;
}
.set-index .head-div {
  width: 100%;
  height: 6.7rem;
  display: block;
  overflow: hidden;
}
.set-index .head-div .img-div {
  width: 3.6rem;
  height: 3.6rem;
  border-radius: 50%;
  display: block;
  overflow: hidden;
  margin: 0.5rem auto 0.2rem auto;
  border: 1px solid #ddd;
}
.set-index .head-div .img-div img {
  width: 3.6rem;
  height: 3.6rem;
}
.set-index .head-div p {
  display: table;
  height: 1rem;
  line-height: 1rem;
  margin: 0 auto;
  font-size: 0.7rem;
}


