
.nav-tab {
  position: relative;
  width: 16rem;
  height: 2rem;
  overflow: hidden;
  background-color: #ccc;
  z-index: 4;
}
.nav-tab .nav-div {
  width: auto;
  height: 1.4rem;
  display: table;
  margin: 0.3rem auto;
  border: 1px solid #ddd;
  border-radius: 1rem;
  overflow: hidden;
  background-color: #fff;
}
.nav-tab .nav-div a {
  min-width: 4rem;
  font-size: 0.7rem;
  padding: 0 0.3rem;
  text-align: center;
  display: inline-block;
  height: inherit;
  line-height: 1.4rem;
  border-right: 1px solid #ccc;
  color: #808080;
}
.nav-tab .nav-div a:last-child {
  border-right: none;
}
.nav-tab .nav-div .is-active {
  background-color: #4fa4d9;
  color: #fff;
}


