
.head {
  width: 100%;
  height: 2.4rem;
  position: relative;
  text-align: center;
  line-height: 2.4rem;
  font-size: 0.85rem;
  border-bottom: 1px solid #b2b2b2;
  background-color: #fff;
  z-index: 4;
}
.head a {
  position: absolute;
  top: 0;
  left: 0;
  width: 2.8rem;
  height: 2.4rem;
  line-height: 2.4rem;
  display: block;
  font-size: 0.75rem;
  padding-right: 0.4rem;
  color: #4fa4d9;
}
.head a:active {
  background-color: #ebebeb;
}
.head a .go-back {
  width: 1rem;
  height: inherit;
  vertical-align: top;
  display: inline-block;
  margin-left: 0.1rem;
  margin-right: -0.2rem;
  background: url("../assets/img/go-back.png") no-repeat center;
  background-size: contain;
}
.head p {
  position: absolute;
  top: 0;
  left: 3.4rem;
  font-size: 0.75rem;
  color: #4fa4d9;
}
.head p:active {
  background-color: #ebebeb;
}
.head i {
  position: absolute;
  right: 0;
  top: 0;
  width: 2.4rem;
  height: 2.4rem;
  line-height: 2.4rem;
  font-size: 1rem;
  color: #4fa4d9;
}
.head i:active {
  background-color: #ebebeb;
}


