
.button_button {
  height: 1.8rem;
  background-color: #44b549;
  border: none;
  color: #fff;
  font-size: 0.7rem;
  border-radius: 0.3rem;
}
.button_button:active {
  background: #3da342;
}
.button_button a {
  width: 100%;
  padding: 0 0.8rem;
  display: block;
  color: #fff;
  box-sizing: border-box;
}
.disabled {
  background-color: #808080;
}
.disabled:active {
  background: #7c7c7c;
}


