
.set {
  width: 16rem;
  height: 100%;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  font-size: 0.6rem;
}
.set .main {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding-top: 2.4rem;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
}
.set .shadow {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #808080;
  z-index: 9;
  opacity: 0.7;
}


